export default class AppView {
  constructor(titles, discription, channelTitle, urlImage, publishedAt, viewCount, id, nextPage) {
    this.titles = titles;
    this.discription = discription;
    this.channelTitle = channelTitle;
    this.urlImage = urlImage;
    this.publishedAt = publishedAt;
    this.viewCount = viewCount;
    this.id = id;
    this.nextPage = nextPage;
  }

  render() {
    const content = document.querySelector('ul');
    this.titles.forEach((el, i) => {
      const li = document.createElement('li');
      li.classList.add('item');
      li.innerHTML = `
      <img src="${this.urlImage[i]}" alt="YouTube" class="item-image">
      <a href="https://www.youtube.com/watch?v=${this.id[i]}" class="item-title">${el}</a>
      <div class="item-center">
        <div class="item-center-left">
          <img src="./image/author.svg" class="item-icon">
          <img src="./image/calendar.svg" class="item-icon">
          <img src="./image/eye.svg" class="item-icon">
        </div>
        <div class="item-center-right">
          <div class="item-content">${this.channelTitle[i]}</div>
          <div class="item-content">${this.publishedAt[i].slice(0, 10)}</div>
          <div class="item-content">${this.viewCount[i]}</div>
        </div>
      </div>
      <p class="item-discription">${this.discription[i]}</p>
      `;
      content.appendChild(li);
    });
  }
}
