import App from './controllers/App';

const searchWrap = document.createElement('div');
searchWrap.classList.add('search-wrap');
searchWrap.innerHTML = `
  <div class="search-container">
    <img src="./image/loupe.svg" class="search-icon">
    <input type="text" class="search" placeholder="Enter the request and press enter">
  </div>
`;
document.body.appendChild(searchWrap);

const searchInput = document.querySelector('.search');

searchInput.addEventListener('keyup', (event) => {
  if (event.keyCode === 13) {
    if (document.querySelector('ul') && document.querySelector('.nav-buttons')) {
      document.querySelector('ul').parentNode.removeChild(document.querySelector('ul'));
      document.querySelector('.nav-buttons').parentNode.removeChild(document.querySelector('.nav-buttons'));
    }
    const searchEl = searchInput.value;
    const app = new App(searchEl);
    app.start();
    const content = document.createElement('ul');
    content.classList.add('items');
    document.body.appendChild(content);

    const buttons = document.createElement('div');

    buttons.classList.add('nav-buttons');
    buttons.innerHTML = `
      <div class="radioButton">
        <input type="radio" id="preprev" class="radioInput" name="nav">
        <label for="preprev" class="label label-prevPrev" data-title=""></label>
      </div>
      <div class="radioButton">
        <input type="radio" id="prev" class="radioInput" name="nav">
        <label for="prev" class="label label-prev" data-title=""></label>
      </div>
      <div class="radioButton">
        <input type="radio" id="current" class="radioInput" name="nav" checked>
        <label for="current" class="label label-current"></label>
      </div>
      <div class="radioButton">
        <input type="radio" id="next" class="radioInput" name="nav">
        <label for="next" class="label label-next" data-title=""></label>
      </div>
    `;
    document.body.appendChild(buttons);

    const slider = document.querySelector('.items');
    const currentButton = document.querySelector('.label-current');
    const widthSize = document.body.clientWidth;
    const buttonNext = document.querySelector('.label-next');
    const buttonPreprev = document.querySelector('.label-prevPrev');
    const buttonPrev = document.querySelector('.label-prev');
    let count = 1;
    let isDown = false;
    let startX;
    let scrollLeftMove;
    let walk;
    let pageNum;

    count = 1;
    currentButton.textContent = count;

    window.addEventListener('resize', () => {
      const widthSize1 = document.body.clientWidth;
      if (widthSize - widthSize1 > 0) {
        if (count >= 1) {
          if (widthSize1 > 1240) {
            pageNum = count;
            currentButton.textContent = count;
          } else if (widthSize1 <= 1240 && widthSize1 > 930) {
            pageNum = Math.floor(((count) * 4) / 3);
            currentButton.textContent = pageNum;
          } else if (widthSize1 <= 930 && widthSize1 > 620) {
            pageNum = Math.floor(((count) * 4) / 2);
            currentButton.textContent = pageNum;
          } else if (widthSize1 <= 620) {
            pageNum = Math.floor(((count) * 4) / 1);
            currentButton.textContent = pageNum;
          }
        }
      }
      if (widthSize - widthSize1 <= 0) {
        if (count > 1) {
          if (widthSize1 > 1240) {
            currentButton.textContent = count;
          } else if (widthSize1 <= 1240 && widthSize1 > 930) {
            pageNum = Math.floor(count / 3);
            currentButton.textContent = pageNum;
          } else if (widthSize1 <= 930 && widthSize1 > 620) {
            pageNum = Math.floor(count / 2);
            currentButton.textContent = pageNum;
          } else if (widthSize1 <= 620) {
            count = pageNum;
            currentButton.textContent = count;
          }
        }
      }
    });

    slider.addEventListener('pointerdown', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.pageX - slider.offsetLeft;
      scrollLeftMove = slider.scrollLeft;
    });

    slider.addEventListener('pointerup', () => {
      isDown = false;
      slider.classList.remove('active');
      if (walk > 0) {
        slider.scroll({
          left: scrollLeftMove - slider.clientWidth,
          behavior: 'smooth',
        });
        count = parseInt(currentButton.textContent, 10);
        if (count > 1) {
          count -= 1;
          currentButton.textContent = count;
        } else {
          currentButton.textContent = 1;
        }
      } else if (walk < 0) {
        slider.scroll({
          left: scrollLeftMove + slider.clientWidth,
          behavior: 'smooth',
        });

        if (slider.scrollWidth - slider.scrollLeft <= slider.clientWidth * 2 + 50) {
          app.startNextPage();
        }
        count = parseInt(currentButton.textContent, 10);
        count += 1;
        currentButton.textContent = count;
      }
    });

    slider.addEventListener('pointermove', (ev) => {
      if (!isDown) return;
      ev.preventDefault();
      const x = ev.pageX - slider.offsetLeft;
      walk = (x - startX);
    });

    buttonNext.addEventListener('pointerdown', () => {
      buttonNext.classList.add('label-next_tooltip');
      count = parseInt(currentButton.textContent, 10);
      buttonNext.setAttribute('data-title', `${count + 1}`);
      isDown = true;
      startX = slider.pageX - slider.offsetLeft;
      scrollLeftMove = slider.scrollLeft;
    });

    buttonNext.addEventListener('pointerup', () => {
      isDown = false;
      buttonNext.classList.remove('label-next_tooltip');

      if (slider.scrollWidth - slider.scrollLeft <= slider.clientWidth * 2 + 50) {
        app.startNextPage();
      }
      count = parseInt(currentButton.textContent, 10);
      count += 1;
      currentButton.textContent = count;
      slider.scroll({
        left: slider.clientWidth + scrollLeftMove,
        behavior: 'smooth',
      });
    });

    buttonPrev.addEventListener('pointerdown', () => {
      buttonPrev.classList.add('label-prev_tooltip');
      count = parseInt(currentButton.textContent, 10);
      if (count > 2) {
        buttonPrev.setAttribute('data-title', `${count - 1}`);
      } else {
        buttonPrev.setAttribute('data-title', '1');
      }
      isDown = true;
      startX = slider.pageX - slider.offsetLeft;
      scrollLeftMove = slider.scrollLeft;
    });

    buttonPrev.addEventListener('pointerup', () => {
      isDown = false;
      buttonPrev.classList.remove('label-prev_tooltip');

      slider.scroll({
        left: scrollLeftMove - slider.clientWidth,
        behavior: 'smooth',
      });
      count = parseInt(currentButton.textContent, 10);
      if (count > 2) {
        count -= 1;
        currentButton.textContent = count;
      } else {
        currentButton.textContent = 1;
      }
    });

    buttonPreprev.addEventListener('pointerdown', () => {
      buttonPreprev.classList.add('label-prevPrev_tooltip');
      count = parseInt(currentButton.textContent, 10);
      if (count >= 3) {
        buttonPreprev.setAttribute('data-title', `${count - 2}`);
      } else {
        buttonPreprev.setAttribute('data-title', '1');
      }

      isDown = true;
      startX = slider.pageX - slider.offsetLeft;
      scrollLeftMove = slider.scrollLeft;
    });

    buttonPreprev.addEventListener('pointerup', () => {
      isDown = false;
      buttonPreprev.classList.remove('label-prevPrev_tooltip');

      slider.scroll({
        left: scrollLeftMove - slider.clientWidth * 2,
        behavior: 'smooth',
      });
      count = parseInt(currentButton.textContent, 10);
      if (count >= 3) {
        count -= 2;
        currentButton.textContent = count;
      } else {
        currentButton.textContent = 1;
      }
    });
  }
});
