import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor(search) {
    this.state = {
      url: `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${search}`,
    };
  }

  async start() {
    const model = new AppModel(this.state);
    const data = await model.getClipNames();
    const description = await model.getClipDescription();
    const channelTitle = await model.getClipChannelTitle();
    const urlImage = await model.getClipUrlImage();
    const publishedAt = await model.getClipPublishedAt();
    const viewCount = await model.getViewCount();
    const id = await model.getId();
    const nextPage = await model.getClipNextPage();
    const view = new AppView(
      data, description, channelTitle, urlImage, publishedAt, viewCount, id, nextPage,
    );
    view.render();
  }

  async startNextPage() {
    const model1 = new AppModel(this.state);
    const nextPage = await model1.getClipNextPage();
    if (this.state.url.indexOf('pageToken') === -1) {
      this.state.url = `${this.state.url}&pageToken=${nextPage}`;
      const model = new AppModel(this.state);
      const data = await model.getClipNames();
      const description = await model.getClipDescription();
      const channelTitle = await model.getClipChannelTitle();
      const urlImage = await model.getClipUrlImage();
      const publishedAt = await model.getClipPublishedAt();
      const viewCount = await model.getViewCount();
      const id = await model.getId();
      const view = new AppView(
        data, description, channelTitle, urlImage, publishedAt, viewCount, id, nextPage,
      );
      view.render();
    } else {
      this.state.url = this.state.url.slice(0, -17);
      this.state.url = `${this.state.url}&pageToken=${nextPage}`;
      const model = new AppModel(this.state);
      const data = await model.getClipNames();
      const description = await model.getClipDescription();
      const channelTitle = await model.getClipChannelTitle();
      const urlImage = await model.getClipUrlImage();
      const publishedAt = await model.getClipPublishedAt();
      const viewCount = await model.getViewCount();
      const id = await model.getId();
      const view = new AppView(
        data, description, channelTitle, urlImage, publishedAt, viewCount, id, nextPage,
      );
      view.render();
    }
  }
}
