export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipNames(data) {
    return data.items.map(clip => clip.snippet.title);
  }

  async getClipNames() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipNames(data);
  }

  static extractClipDescription(data) {
    return data.items.map(clip => clip.snippet.description);
  }

  async getClipDescription() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipDescription(data);
  }

  static extractClipChannelTitle(data) {
    return data.items.map(clip => clip.snippet.channelTitle);
  }

  async getClipChannelTitle() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipChannelTitle(data);
  }

  static extractClipUrlImage(data) {
    return data.items.map(clip => clip.snippet.thumbnails.medium.url);
  }

  async getClipUrlImage() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipUrlImage(data);
  }

  static extractClipPublishedAt(data) {
    return data.items.map(clip => clip.snippet.publishedAt);
  }

  async getClipPublishedAt() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipPublishedAt(data);
  }

  static extractId(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  async getId() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractId(data);
  }

  static extractViewCount(dataView) {
    return dataView.items.map(clip => clip.statistics.viewCount);
  }

  async getViewCount() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    const viewEl = AppModel.extractId(data).join(',');
    const urlView = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${viewEl}&part=snippet,statistics`;
    const responceView = await fetch(urlView);
    const dataView = await responceView.json();
    return AppModel.extractViewCount(dataView);
  }

  static extractClipNextPage(data) {
    return data.nextPageToken;
  }

  async getClipNextPage() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipNextPage(data);
  }
}
